a {
  text-decoration: none;
  color: #E0ECFF;
}
.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 72px;
}
.header .menu {
  display: flex;
  transform: inherit;
}
.header .left {
  display: flex;
  align-items: center;
}
.header .left .logo {
  margin-left: 48px;
  display: flex;
  align-items: center;
}
.header .left .logo > a {
  color: #E0ECFF;
  text-decoration: none;
  display: flex;
}
.header .left .logo .name {
  margin: auto 12px;
  color: #E0ECFF;
  font-size: 20px;
}
.header .right {
  display: flex;
  margin-right: 24px;
}
.header .small-menu {
  margin-left: 24px;
}
@-moz-keyframes fadeout {
  0% {
    transform: translateX(0px);
  }
  100% {
    transform: translateX(-100%);
  }
}
@-webkit-keyframes fadeout {
  0% {
    transform: translateX(0px);
  }
  100% {
    transform: translateX(-100%);
  }
}
@keyframes fadeout {
  0% {
    transform: translateX(0px);
  }
  100% {
    transform: translateX(-100%);
  }
}
@media only screen and (max-width: 375px) {
  .header .left .logo {
    margin-left: 32px;
  }
  .header .menu {
    display: flex;
    transform: translateX(-100%);
    -moz-animation: fadeout 0.2s 0s linear;
    -webkit-animation: fadeout 0.2s 0s linear;
    animation: fadeout 0.2s 0s linear;
  }
  .header .small-menu {
    display: block;
  }
  .header .lang-select,
  .header .network-select {
    display: none;
  }
}
@media only screen and (max-width: 760px) {
  .header .left .logo {
    margin-left: 32px;
  }
  .header .menu {
    display: flex;
    transform: translateX(-100%);
    -moz-animation: fadeout 0.2s 0s linear;
    -webkit-animation: fadeout 0.2s 0s linear;
    animation: fadeout 0.2s 0s linear;
  }
  .header .small-menu {
    display: block;
  }
  .header .lang-select,
  .header .network-select {
    display: none;
  }
}
@media only screen and (max-width: 1140px) {
  .header .left .logo {
    margin-left: 32px;
  }
  .header .menu {
    display: flex;
    transform: translateX(-100%);
    -moz-animation: fadeout 0.2s 0s linear;
    -webkit-animation: fadeout 0.2s 0s linear;
    animation: fadeout 0.2s 0s linear;
  }
  .header .small-menu {
    display: block;
  }
  .header .lang-select,
  .header .network-select {
    display: none;
  }
}
@media only screen and (min-width: 1141px) {
  .header .menu {
    display: flex;
    transform: inherit;
  }
  .header .small-menu {
    display: none;
  }
  .header .lang-select,
  .header .network-select {
    display: flex;
  }
}
